.navbar-container {
  width: 100%;
  height: 120px;
  background-color: rgb(239, 208, 238);
  padding: 0 20px;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 500;
  border-bottom: 3px solid rgb(169, 0, 221);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.15);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.navbar-side-items {
  width: 150px;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.navbar-title {
  font-family: "Nunito";
  font-size: 3.5rem;
  font-weight: 400;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  color: blueviolet;
}

.sentiment {
  margin: 1.2rem;
  margin-bottom: 1.5rem;
  font-size: 2.5rem;
  font-family: fantasy;
  font-weight: 900;
  color: blueviolet;
}
